body {
  margin: 0;
  background-color: black;
  color: white;
}

@media screen and (max-width: 768px) {
  /* Ogólne style dla responsywności na telefony */
  .App {
    text-align: left;
    padding: 10px;
  }

  .TodoList {
    max-width: none;
    margin: 0;
    padding: 10px;
  }

  input {
    width: 100%;
    margin-bottom: 10px;
  }

  button {
    margin: 0.5rem 0;
    width: 100%;
  }

  .completed {
    text-decoration: none;
  }

  /* Style dla strony głównej */
  .home-container {
    min-height: auto;
  }

  .home-content {
    text-align: left;
  }

  /* Style dla formularza kontaktowego */
  .contact-form {
    height: auto;
  }

  .contact-form label {
    text-align: left;
  }

  .contact-form input,
  .contact-form textarea {
    width: 100%;
    margin-bottom: 10px;
  }

  .contact-form button {
    width: 100%;
  }
}

/* Pozostałe style */
body {
  color: black;
  background-color: #f2f2f2;
}

/* Nawigacja */
.navigation {
  display: flex;
  justify-content: center;
  background-color: rgba(189, 0, 255, 1);
  background-image: linear-gradient(
    297deg,
    rgba(189, 0, 255, 1) 21%,
    rgba(0, 98, 255, 1) 100%
  );
}

.navigation ul {
  display: flex;
  justify-content: center;
  padding: 0;
  list-style: none;
}

.navigation li {
  margin: 0 10px;
}

.navigation a {
  color: white;
  text-decoration: none;
  font-size: 2.5rem;
}

.navigation a:hover {
  color: lightblue;
}

.Contact label {
  font-size: 1.5rem;
  margin-right: 1rem;
}

.Contact button {
  margin-top: 4rem;
  font-family: Roboto, sans-serif;
  font-weight: 700;
  font-size: 1.5rem;
  color: #fff;
  background-color: #0066cc;
  padding: 10px 30px;
  border: solid #8fb4d9 4px;
  border-radius: 0px 25px 25px 50px;
  transition: 1000ms;
  transform: translateY(0);
  display: flex;
  flex-direction: row;
  align-items: center;
  cursor: pointer;
}

.Contact button:hover {
  transition: 1000ms;
  padding: 10px 50px;
  transform: translateY(-0px);
  background-color: #fff;
  color: #0066cc;
  border: solid 4px #0066cc;
}

input {
  padding: 5px;
  font-size: 16px;
  border-width: 1px;
  border-color: #cccccc;
  background-color: #ffffff;
  color: #000000;
  border-style: solid;
  border-radius: 0px;
  box-shadow: 0px 0px 5px rgba(66, 66, 66, 0.75);
}

input:focus {
  outline: none;
}

textarea {
  padding: 5px;
  font-size: 16px;
  border-width: 1px;
  border-color: #cccccc;
  background-color: #ffffff;
  color: #000000;
  border-style: solid;
  border-radius: 0px;
  box-shadow: 0px 0px 5px rgba(66, 66, 66, 0.75);
}

textarea:focus {
  outline: none;
}

.addButton {
  min-width: 130px;
  height: 40px;
  color: #fff;
  padding: 5px 10px;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  display: inline-block;
  outline: none;
  border-radius: 5px;
  border: none;
  box-shadow: inset 2px 2px 2px 0px rgba(255, 255, 255, 0.5),
    7px 7px 20px 0px rgba(0, 0, 0, 0.1), 4px 4px 5px 0px rgba(0, 0, 0, 0.1);
  background: #4433ff;
}

.addButton:hover {
  background-color: #3a86ff;
}

.addButton:active {
  top: 2px;
}

.delete-button {
  min-width: 130px;
  height: 40px;
  color: #fff;
  padding: 5px 10px;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  display: inline-block;
  outline: none;
  border-radius: 5px;
  border: none;
  box-shadow: inset 2px 2px 2px 0px rgba(255, 255, 255, 0.5),
    7px 7px 20px 0px rgba(0, 0, 0, 0.1), 4px 4px 5px 0px rgba(0, 0, 0, 0.1);
  background: #ff3333;
}

.delete-button:hover {
  background-color: #f05d5d;
}

.addButton :active {
  top: 2px;
}

.complete-button {
  min-width: 130px;
  height: 40px;
  color: #fff;
  padding: 5px 10px;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  display: inline-block;
  outline: none;
  border-radius: 5px;
  border: none;
  box-shadow: inset 2px 2px 2px 0px rgba(255, 255, 255, 0.5),
    7px 7px 20px 0px rgba(0, 0, 0, 0.1), 4px 4px 5px 0px rgba(0, 0, 0, 0.1);
  background: #00e043;
}

.complete-button:hover {
  background-color: #07e49a;
}

.complete-button :active {
  top: 2px;
}

.Contact {
  max-width: 400px;
  padding: 0.2rempx;
  text-align: center;
}

.form-grid {
  display: grid;
  grid-gap: 0.1rem;
}

.form-row {
  display: grid;
  grid-template-columns: 1fr;
  align-items: center;
}

.Contact label {
  text-align: left;
  padding-right: 10px;
  padding-bottom: 1rem;
}

.Contact input,
.Contact textarea {
  width: 100%;
}

.submitButton {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
  width: 15rem;
}
